.industries{
    margin-bottom: 160px;
    &__inner{
        &-subtitle{
            margin: 0 auto;
            margin-bottom: 12px;
        }
        &-title{
            text-align: center;
            margin-bottom: 8px;
        }
        &-descr{
            text-align: center;
            margin: 0 auto; 
            max-width: 777px;
            font-size: 18px;
            font-weight: 400;
            line-height: 28px;
            color: $text-clr;
        }
    }
    &__box{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        position: relative;
        overflow: hidden;
        &-item{
            max-width: 672px;
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding: 32px 0 36px 0;
        }
        &-img{
            margin-bottom: 32px;
        }
        &-title{
            margin-bottom: 8px;
            font-weight: 600;
            font-size: 20px;
            color: $main-clr;
        }
        &-text{
            margin-bottom: 8px;
            max-width: 545px;
            text-align: center;
            font-size: 18px;
            font-weight: 400;
            color: $main-clr;
            line-height: 28px;
        }
        &-link{
            color: $link-clr-second;
            font-size: 16px;
            font-weight: 500;
            text-decoration: underline;
        }
        &-decor{
            position: absolute;
            width: 16px;
            height: 16px;
            border-radius: 2px;
            background-color: $second-accent-clr;
            transform: rotate(135deg) translate(-50%, -50%);
            top: calc(50% - 8px);
            left: calc(50% - 19px);
            @media (max-width: 1390px){
                display: none;
            }
        }
        &-line{
            &--horizontal{
                position: absolute;
                width: 1344px;
                height: 1px;
                left: 50%;
                top: 50%;
                background: rgb(237,248,248);
                background: linear-gradient(90deg, rgba(237,248,248,1) 10%, rgba(170,221,221,1) 50%, rgba(237,248,248,1) 90%);
                transform: translate(-50%, -50%);
                z-index: -1;
                @media (max-width: 1390px){
                    display: none;
                }
            }
            &--vertical{
                position: absolute;
                width: 592px;
                height: 1px;
                background: rgb(237,248,248);
                background: linear-gradient(90deg, rgba(237,248,248,1) 10%, rgba(170,221,221,1) 50%, rgba(237,248,248,1) 90%);
                top: 50%;
                left: calc(50% + 1px);
                transform: translate(-50%, -50%) rotate(90deg);
                z-index: -1;
                @media (max-width: 1390px){
                    display: none;
                }
            }
        }
    }
}