.philosophy{
    &-inner{
        @media (max-width: $breakpoint-sm) {
            display: flex;
            flex-direction: column;
            gap: 40px;
        }
        &__block{
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 10px;
            @media (max-width: $breakpoint-sm) {
                flex-direction: column;
            }
            &--vision{
                margin: -4px;
            }
            &-image{
                @media (max-width: $breakpoint-sm) {
                    order: -1;
                }
            }
            &-img{
                width: 100%;
            }
            &-content{
                margin: 0 65px 0 96px;
                font-weight: 600;

                @media (max-width: $breakpoint-lg) {
                    margin-left: 0;    
                }
                &--vision{
                    order: 1;
                    @media (max-width: $breakpoint-lg) {
                        margin-left: 65px;
                        margin-right: 0;
                    }
                    @media (max-width: $breakpoint-sm) {
                        margin: 0;
                    }
                }
            }
            &-title{
                font-size: 32px;
                color: #14293E;
                margin-bottom: 48px;
                @media (max-width: $breakpoint-lg) {
                    font-size: 24px;
                    margin-bottom: 24px;
                }
                @media (max-width: $breakpoint-md) {
                    font-size: 18px;
                    margin-bottom: 15px;
                }
            }
            &-text{
                font-size: 24px;
                line-height: 40px;
                color: #496179;
                max-width: 487px;
                display: flex;
                align-items: center;
                border-left: 2px solid #46B5B6;
                padding-left: 30px;
                @media (max-width: $breakpoint-lg) {
                    font-size: 16px;
                    line-height: 22px;
                }
                @media (max-width: $breakpoint-md) {
                    font-size: 14px;
                    line-height: 20px;
                }
            }
        }
    }
}