.contact-form {
    &__inner {
        display: flex;
        padding: 0 160px 88px 88px;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        @media (max-width: calc($breakpoint-lg + 295px)) {
            padding: 0;
            justify-content: center;
            gap: 40px;
        }
    }

    &__form {
        &-box {
            max-width: 625px;
            width: 100%;
            padding: 48px;
            background-color: #F4F8FB;
            @media (max-width: $breakpoint-sm){
                padding: 48px 24px;
            }
        }
        &-title{
            font-size: 32px;
            font-weight: 600;
            color: $main-clr;
            @media (max-width: $breakpoint-sm){
                font-size: 27px;
            }
        }
        &-form{
            margin-top: 40px;
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 24px;
        }
        &-item{
            max-width: 530px;
            width: 100%;
            display: flex;
            flex-direction: column;
            gap: 8px;

        }
        &-label{
            color: #496179;
            font-size: 18px;
            font-weight: 500;
            @media (max-width: $breakpoint-sm){
                font-size: 16px;
            }
        }
        &-input{
            border: none;
            border-radius: 8px;
            padding: 10px 16px 10px 16px;
        }
        &-textarea{
            border: none;
            border-radius: 8px;
            height: 136px;
            resize: none;
            padding: 8px 16px;
        }
        &-button{
            margin-top: 40px;
            background-color: #40A5A6;
            color: #F1F3F5;
            border-radius: 4px;
            max-width: 530px;
            width: 100%;
            padding: 16px 0;
        }


    }
    &__more{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 645px;
        &-item{
            border-bottom: 1px solid #B9C2CB;
            padding-bottom: 28px;
        }
        &-title{
            font-size: 20px;
            font-weight: 600;
            color: #496179;
            margin-bottom: 4px;
        }
        &-subtitle{
            color: #14293E;
            font-weight: 400;
            font-size: 18px;
            margin-bottom: 14px;
        }
        &-link{
            color: #14293E;
            font-weight: 600;
            font-size: 16px;
            text-decoration: underline;
        }
    }
}

.db-mb-12{
    display: block;
    margin-bottom: 12px;
}