.experience{
    margin-top: 100px;
    &-inner{
        &__content{
            display: flex;
            max-width: 1337px;
            margin: 0 auto;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 65px;
            @media (max-width: $breakpoint-md) {
                flex-direction: column;
                gap: 40px;
                align-items: flex-start;
            }
            &-title{
                font-size: 48px;
                font-weight: 600;
                line-height: 64px;
                color: #14293E;
                max-width: 550px;
                &--green{
                    color: #40A5A6;
                }
                @media(max-width: $breakpoint-lg){
                    font-size: 36px;
                    line-height: 42px;
                    max-width: 420px;
                }
                @media(max-width: $breakpoint-md){
                    max-width: 100%;
                }
            }
            &-text{
             font-size: 20px;
             font-weight: 400;
             line-height: 32px;
             color: #1C3A57;
             padding-left: 25px;
             border-left: 3px solid #46B5B6;
             max-width: 496px;
            @media(max-width: $breakpoint-lg){
                font-size: 18px;
                line-height: 25px;
                max-width: 320px;
            }
            @media(max-width: $breakpoint-md){
                max-width: 100%;
            }
            }
        }
        &__image{
            position: relative;
            &::before{
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 400px;
                background-color: #40A5A6;
                opacity: .3;
            }
            &-img{
                min-height: 400px;
                width: 100%;
                object-fit: cover;
            }
            @media (max-width: $breakpoint-sm) {
                display: none;
            }
        }
    }
}