.faq{
    margin-bottom: 96px;
    &-inner{
        &__title{
            color: #14293E;
            font-size: 32px;
            font-weight: 600;
            text-transform: capitalize;
            margin-bottom: 16px;
            text-align: center;
        }
        &__subtitle{
            color: #496179;
            font-size: 18px;
            font-weight: 500;
            text-align: center;
            max-width: 310px;
            margin: 0 auto;
            margin-bottom: 56px;
        }
        &__accordion{
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            max-width: 1120px;
            margin: 0 auto;
            &-details{
                max-width: 550px;
                width: 100%;
                gap: 16px;
                padding: 18px 0;
                border-bottom: 1px solid #E8EBEE;
                &[open] {
                    .faq-inner__accordion-svg {
                        transform: rotate(180deg);
                    }
                }
            }
            &-summary{
                list-style: none;
                display: flex;
                align-items: center;
                gap: 16px;
                color: #496179;
                font-size: 20px;
                font-weight: 400;
                line-height: 28px;
                cursor: pointer;
            }
            &-svg{
                transition: transform .3s ease-in-out;
            }
            &-paragraph{
                padding-left: 40px;
                color: $main-clr;
                font-size: 18px;
                font-weight: 400;
            }
        }
    }
}