.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;
    background-color: #fff;
    &__inner{
        display: flex;
        align-items: center;
        justify-content: space-around;
        min-height: 100px;
        &-button{
            max-width: 300px;
            width: 100%;
            background-color: $accent-clr;
            height: 100px;
            border: none;
            color: #F1F3F5;
            padding: 0;
            margin: 0;
            margin-left: 20px;
            font-size: 18px;
            transition: all .3s ease-in-out;
            &:hover{
                border: 2px solid $desc-clr;
                background-color: transparent;
                color: $desc-clr;
            }
        }
    }
    &-placeholder{
        height: 100px;
        width: 100%;
    }

}

.logo{
    margin-right: 20px;
    &__link{
        cursor: pointer;
    }
    &__img{
        width: 95px;
        position: relative;
        z-index: 5;
    }
}

.menu{
    &__list{
        &-item{
            position: relative;
        }
        &-link{
            font-size: 18px;
            color: $main-clr;
            display: flex;
            align-items: center;
            gap: 10px;
            &:hover{
                text-decoration: underline;
            }
        }
        &-icon{
            transition: transform .2s ease-in-out;
        }
        &-button{
            display: none;
        }
    }
    &-sub__list{
        position: absolute;
        top: 100%;
        left: 0;
        background-color: #f1ebeb;
        padding: 15px;
        min-width: 150px;
        border-bottom-right-radius: 10px;
        border-bottom-left-radius: 10px;
        &-item{
            margin-bottom: 10px;
            &:last-child{
                margin-bottom: 0;
            }
        }
        &-link{
            color: $main-clr;
            &:hover{
                text-decoration: underline;
            }
        }
    }
    &__icon{
        display: none;
    }
}

body.--pc{
    .menu {
        &__list{
            &-item{
                &:hover{
                    .menu-sub__list {
                        opacity: 1;
                        visibility: visible;
                        transform: translate(0px, 0%);
                        pointer-events: all;
                    }
                }
            }
        }
    }
}

body.--touch{
    .menu{
        &__list{
            &-item{
                &--dropdown.--active{
                    .menu-sub__list {
                        opacity: 1;
                        visibility: visible;
                        transform: translate(0px, 0%);
                        pointer-events: all;
                    }
                    .menu__list-icon{
                        transform: rotate(180deg);
                    }
                }
            }
            &-link{
                flex: 1 1 auto;
            }
        }
    }
}

body.--lock{
    overflow: hidden;
}

@media (min-width: $breakpoint-md) {
    .menu{
        &__list{
            display: flex;
            align-items: center;
            gap: 20px;
            &-item{
                padding: 10px 0;
            }
        }
        &-sub__list{
            transform: translate(0px, 10%);
            opacity: 0;
            visibility: hidden;
            pointer-events: none;
            transition: all .3s ease-in;
        }
    }
}

@media (max-width: calc($breakpoint-md - 1px)) {
    .header{
        width: 96%;
        &__inner{
            justify-content: space-between;
            &-button{
                display: none;
            }
        }
    }
    .menu{
        &__icon{
            z-index: 5;
            display: block;
            position: relative;
            width: 30px;
            height: 18px;
            cursor: pointer;
            &::after,
            &::before,
            span{
                position: absolute;
                left: 0;
                height: 10%;
                width: 100%;
                transition: all .3s ease;
                background-color: $main-clr;
            }
            &::after,
            &::before{
                content: "";
            }
            &::after{
                bottom: 0;
            }
            &::before{
                top: 0;
            }
            span{
                top: 50%;
                transform: scale(1) translateY(-50%);
            }
            &.--active{
                span{
                    transform: scale(0);
                }
                &::before{
                    top: 50%;
                    transform: rotate(-45deg) translateY(-50%);
                }
                &::after{
                    bottom: 50%;
                    transform: rotate(45deg) translateY(50%);
                }
            }
        }
        &__body{
            position: fixed;
            top: 0;
            left: -100%;
            width: 100%;
            height: 100%;
            background-color: rgba(255, 255, 255, .9);
            padding: 130px 30px 30px 30px;
            transition: left 0.3s ease;
            overflow-y: scroll;
            &::before{
                content: "";
                position: fixed;
                width: 100%;
                top: 0;
                left: 0;
                height: 100px;
                background-color: #fff;
                z-index: 2;
            }
            &.--active{
                left: 0;
            }

        }
        &__list{
            &-item{
                margin-bottom: 30px;
                flex-wrap: wrap;
                &:last-child{
                    margin-bottom: 0;
                }
                &--dropdown.--active{
                    .menu-sub__list{
                        display: block;
                    }
                }
            }
            &-link{
                font-size: 24px;
            }
            &-button{
                max-width: 300px;
                width: 100%;
                background-color: $accent-clr;
                height: 50px;
                border: none;
                color: #F1F3F5;
                padding: 0;
                margin-top: 50px;
                font-size: 24px;
            }
        }
        &-sub__list{
            position: relative;
            background-color: #fff;
            flex: 1 1 100%;
            margin-top: 20px;
            display: none;
            &-link{
                font-size: 20px;
                color: #000;
            }
        }
        
    }
}

