.footer{
    margin-bottom: 32px;
    overflow: hidden;

    &-inner{
        &__header{
            padding-bottom: 115px;
            display: flex;
            flex-direction: column;
            align-items: center;
            &-background{
                position: absolute;
                z-index: 2;
                max-height: 360px;
                max-width: 1442px;
                width: 100%;
                height: 460px;
                background-color: #46B5B6;
            }
            &-img{
                position: absolute;
                opacity: .2;
                z-index: 1;
                width: 100%;
                min-height: 360px;
                object-fit: cover;
            }
            &-title{
                margin-top: 96px;
                position: relative;
                z-index: 5;
                color: #FBFDFE;
                font-size: 32px;
                font-weight: 600;
                @media (max-width: $breakpoint-sm) {
                    margin-top: 45px;
                }
            }
            &-text{
                position: relative;
                z-index: 5;
                color: #EEF4F9;
                font-size: 18px;
                font-weight: 400;
                margin-bottom: 36px;
            }
            &-box{
                position: relative;
                z-index: 5;
                display: flex;
                gap: 16px;
                @media (max-width: $breakpoint-sm) {
                    flex-wrap: wrap;
                }
            }
            &-btn{
                &:first-child{
                    font-weight: 500;
                    color: #FBFDFE;
                    border: 1px solid #FBFDFE;
                    border-radius: 4px;
                    padding: 16px 61px;
                }
                &:nth-child(2){
                    font-weight: 600;
                    color: #46B5B6;
                    text-transform: capitalize;
                    background-color: #FBFDFE;
                    padding: 16px;
                }
                @media (max-width: $breakpoint-sm) {
                    width: 100%;
                }
            }
        }
        &__body{
            padding: 48px 48px 0 48px;
            margin-bottom: 48px;
            &-menu{
                max-width: 1344px;
                display: flex;
                justify-content: space-between;
                @media (max-width: $breakpoint-lg){
                    gap: 20px;
                    flex-wrap: wrap;
                    justify-content: center;
                }
            }
            &-title{
                font-size: 20px;
                font-weight: 600;
                color: #14293E;
                margin-bottom: 24px;
            }
            &-item{
                margin-bottom: 16px;
                display: flex;
                gap: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                address{
                    font-style: normal;
                }

            }
            &-link{
                font-size: 16px;
                font-weight: 400;
                color: #496179;
            }
        }
        &__contact-title{
            color: #496179;
            font-size: 20px;
            font-weight: 600;
            margin-bottom: 20px;
        }
        &__folow{
            margin-top: 36px;
            &-title{
                color: #14293E;
                font-size: 20px;
                font-weight: 600;
                margin-bottom: 16px;
            }
            &-list{
                display: flex;
                gap: 24px;
            }
        }
        &__footer{
            padding: 0 48px;
            @media (max-width: $breakpoint-sm) {
                padding: 0;
            }
            &-form{
                margin-bottom: 44px;
                display: flex;
                @media (max-width: $breakpoint-sm) {
                    flex-wrap: wrap;
                    gap: 20px;
                }
            }
            &-label{
                position: absolute;
                width: 1px;
                height: 1px;
                padding: 0;
                margin: -1px;
                overflow: hidden;
                clip: rect(0, 0, 0, 0);
                white-space: nowrap;
                border: 0;
            }
            &-input{
                max-width: 440px;
                width: 100%;
                padding: 14px 20px 14px 8px;
                border: 1px solid #97A4B2;
                border-radius: 8px;
                color: $main-clr;
                @media (max-width: $breakpoint-sm) {
                    max-width: 100%;
                }
            }
            &-button{
                margin-left: 16px;
                color: #F1F3F5;
                font-size: 18px;
                font-weight: 600;
                padding: 16px 65px;
                background-color: #40A5A6;
                border-radius: 4px;
                @media (max-width: $breakpoint-sm) {
                    margin: 0;
                    width: 100%;
                }
            }
            &-line{
                margin-bottom: 32px;
                width: 100%;
                color: #C6E8E8;
            }
            &-box{
                display: flex;
                justify-content: space-between;
                color: #677B8E;
                @media (max-width: $breakpoint-sm) {
                    flex-wrap: wrap-reverse;
                    gap: 40px;
                }
            }
            &-list{
                display: flex;
                gap: 32px;
            }
            &-item{
                position: relative;
                &:nth-child(1)::after,
                &:nth-child(2)::after{
                    content: "";
                    width: 1px;
                    height: 28px;
                    background-color: #677B8E;
                    position: absolute;
                    top: 50%;
                    right: -16px;
                    transform: translateY(-50%);
                }
            }
        }
    }
}

.contact-page .footer {
    background-image: url(../../img/footer-bg.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: left calc(100% + 100px);
    &-inner{
        &__header{
            &-img{
                display: none;
            }
        }
        &__footer{
            &-input{
                background-color: transparent;
            }
        }
    }

}