.contact-promo{
    padding: 96px 0;
    position: relative;
    &__bg{
        position: absolute;
        top: -10px;
        left: 0;
        z-index: -1;
    }
    &__inner{
        text-align: center;
        max-width: 553px;
        margin: 0 auto;
        &-title{
            color: #14293E;
            font-weight: 600;
            font-size: 48px;
            margin-bottom: 8px;
        }
        &-subtitle{
            font-size: 18px;
            font-weight: 400;
            color: #496179;
        }
    }
}

.background{
    overflow: hidden;
    &-img{
        position: absolute;
        width: 100%;
        height: 100%;    
        z-index: -1;
        top: 100px;
        left: 0;
    }

}