.promo{
    text-align: center;
    background-image: url(../img/promo-bg.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    @media (min-width: $breakpoint-md){
        min-height: 800px;   
    }

    &__inner{
        padding-top: 72px;
        display: flex;
        flex-direction: column;
        align-items: center;
        &-title{
            font-size: 48px;
            font-weight: 600;
            line-height: 56px;
            max-width: 660px;
            margin-bottom: 16px;
            color: $main-clr;

            @media (max-width: $breakpoint-md){
                font-size: 32px;
                line-height: 46px;
            }
        }
        &-text{
            line-height: 28px;
            font-weight: 400;
            max-width: 725px;
            color: $desc-clr;
            margin-bottom: 32px;
        }
        &-buttons{
            display: flex;
            gap: 16px;
            margin-bottom: 52px;
        }
        &-button{
            height: 56px;
            font-weight: 500;
            color: $desc-clr;
            width: 205px;
            border: 1px solid $desc-clr;
            border-radius: 4px;
            transition: all .3s ease-in-out;
            &:hover{
                background-color: $accent-clr;
                color: $main-white-clr;
                border: none;
            }
            &--with-bg{
                border: none;
                background-color: $accent-clr;
                width: 230px;
                color: $main-white-clr;
                font-weight: 600;
                &:hover{
                    border: 1px solid $desc-clr;
                    background-color: transparent;
                    color: $desc-clr;
                }
            }
            @media (max-width: $breakpoint-md){
                width: 140px;
            }
        }
    }
    &__box{
        &-img{
            width: 100%;
        }
    }
}

