.team{
    margin-top: 95px;
    &-inner{
        &__content{
            text-align: center;
            margin-bottom: 55px;
            &-title{
                font-size: 32px;
                font-weight: 600;
                text-transform: capitalize;
                color: #14293E;
                margin-bottom: 16px;
            }
            &-text{
                margin: 0 auto;
                max-width: 665px;
                font-size: 18px;
                font-weight: 400;
                line-height: 28px;
                color: #496179;
            }
        }
        &__participants{
            display: flex;
            gap: 16px;
            max-width: 1344px;
            margin: 0 auto;
            margin-bottom: 65px;
            @media (max-width: $breakpoint-lg) {
                flex-wrap: wrap;
                justify-content: center;
                gap: 30px;
                
            }
            &-participant{
                text-align: center;
                display: flex;
                flex-direction: column;
                max-width: 324px;
                position: relative;
            }
            &-image{
                min-height: 408px;
                max-width: 324px;
                width: 100%;
                margin-bottom: 24px;
            }
            &-fullname{
                font-size: 24px;
                font-weight: 500;
                color: #14293E;
                margin-bottom: 8px;
                position: relative;
                z-index: 2;
            }
            &-position{
                font-weight: 500;
                font-size: 18px;
                font-style: italic;
                color: #496179;
                position: relative;
                z-index: 2;
            }
            &-link{
                &::after{
                    content: "";
                    display: block;
                    position: absolute;
                    inset: 0;
                }
            }
        }
        &__button{
            margin: 0 auto;
            display: block;
            padding: 16px 45px;
            border: 1px solid $main-clr;
            color: $main-clr;
            text-transform: capitalize;
        }
    }
}