h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

*,
*::after,
*::before{
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: inherit;
}

button{
    border: none;
    background-color: transparent;
    cursor: pointer;
}

ul{
    list-style-type: none;
}

.container{
    max-width: 1460px;
    padding: 0 10px;
    margin: 0 auto;
}