.article{
    margin-bottom: 96px;
    &-insights{
        &__box{
            margin-bottom: 40px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            @media (max-width: $breakpoint-sm) {
                flex-wrap: wrap;
                gap: 15px;
            }
            &-content{
                max-width: 665px;
                margin-right: 20px;
            }
            &-title{
                color: $main-clr;
                font-size: 32px;
                font-weight: 600;
            }
            &-text{
                color: $text-clr;
                font-size: 18px;
                line-height: 28px;
                font-weight: 500;
            }
        }
        &__button{
            border: 1px solid $main-clr;
            border-radius: 4px;
            color: $main-clr;
            padding: 16px 75px;
            @media (max-width: $breakpoint-md) {
                padding: 5px 50px;
                
            }
            @media (max-width: $breakpoint-sm) {
                padding: 16px 0;
                width: 100%;                
            }

        }
    }
    &__inner{
        padding: 0 58px;
        @media (max-width: $breakpoint-md){
            padding: 0;
        }
        &-box{
            display: flex;
            gap: 16px;
            justify-content: center;
            @media (max-width: calc($breakpoint-lg + 300px)){
                flex-wrap: wrap;
            }
            @media (max-width: $breakpoint-md){
                gap: 40px;
            }
        }
        &-item{
            max-width: 437px;
            width: 100%;
            transition: color .3s ease-in-out;
            display: flex;
            flex-direction: column;
            align-items: center;
            position: relative;
            &:hover{
                .article__inner-title{
                    color: $main-clr;
                }
                .article__inner-text{
                    color: $reviewer-color;
                }
            }
        }
        &-image{
            // min-height: 425px;
            position: relative;
            margin-bottom: 24px;
            &::before{
                content: "";
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: calc(100% - 3px);
                // min-height: 425px;
                background-color: rgba(0, 0, 0, 0.2);
            }
        }
        &-img{
            width: 100%;
        }
        &-content{
            max-width: 405px;
        }
        &-title{
            font-size: 20px;
            font-weight: 600;
            line-height: 28px;
            color: #3A3A3A;
            position: relative;
            z-index: 2;
        }
        &-text{
            font-size: 18px;
            line-height: 28px;
            font-weight: 400;
            color: $link-clr-second;
            position: relative;
            z-index: 2;
        }
        &-link{
            color: $main-clr;
            font-size: 18px;
            font-weight: 500;
            text-decoration: underline;
            text-transform: capitalize;
            &::after{
                content: "";
                display: block;
                position: absolute;
                inset: 0;
            }
        }
    }
}