.reviews{
    margin-bottom: 150px;
    &__inner{
        &-subtitle{
            margin: 0 auto;
            margin-bottom: 8px;
        }
        &-title{
            text-align: center;
            margin-bottom: 48px;
        }
    }
    &__carousel{
        position: relative;
        @media (max-width: $breakpoint-lg){
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
        &-btn{
            border: 1px solid $accent-clr;
            border-radius: 50%;
            padding: 24px 19px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            z-index: 2;
            background-color: #fff;
            &--right{
                right: 0;
                @media (min-width: $breakpoint-lg) {
                    right: 100px;
                }
            }
            &--left{
                left: 0;
                @media (min-width: $breakpoint-lg) {
                    left: 100px;
                }
            }
        }
        &-arrow{
            &--left{
                transform: rotate(180deg);
            }
        }
        &-text{
            font-size: 24px;
            line-height: 32px;
            font-weight: 400;
            text-align: center;
            max-width: 830px;
            color: $desc-clr;
            margin: 0 auto 16px auto;
            @media (max-width: $breakpoint-sm){
                font-size: 18px;
            }
        }
        &-reviewer{
            display: block;
            text-align: center;
            font-size: 18px;
            line-height: 24px;
            color: $reviewer-color;
            @media (max-width: $breakpoint-sm){
                font-size: 16px;
            }
        }
    }
}

.slick-dots{
    display: flex;
    margin: 0 auto;
    gap: 20px;
    li{
        button{
            font-size: 0;
            border-radius: 50%;
            width: 20px;
            height: 20px;
            border: 1px solid $main-clr;
        }
        &.slick-active{
            button{
                background-color: $main-clr;
                border: none;
            }
        }

    }
}