.achievements{
    margin-top: 80px;
    overflow: hidden;
    &-inner{
        &__content{
            text-align: center;
            margin-bottom: 55px;
            &-title{
                font-size: 32px;
                color: #14293E;
                font-weight: 600;
                margin-bottom: 16px;
                text-transform: capitalize;
            }
            &-text{
                max-width: 1118px;
                line-height: 32px;
                font-size: 18px;
                font-weight: 400;
                color: #496179;
                margin: 0 auto;
            }
        }
        &__statistic{
            width: 100%;
            display: flex;
            justify-content: space-around;
            padding: 56px 0;
            background-color: #40A5A6;
            position: relative;
            @media (max-width: $breakpoint-sm) {
                flex-wrap: wrap;
                gap: 25px;   
                padding: 25px;             
            }
            &-bg{
                position: absolute;
                width: 100vh;
                height: 100%;
                background-color: #40A5A6;
                margin-top: -56px;
                z-index: -1;
                @media (max-width: $breakpoint-sm) {
                    margin-top: -25px; 
                }
            }
            &-item{
                display: flex;
                flex-direction: column;
                gap: 8px;
                color: #EBEBEB;
                justify-content: center;
                align-items: center;

            }
            &-number{
                font-size: 80px;
                font-weight: 700;
                @media (max-width: $breakpoint-md) {
                    font-size: 50px;
                }
            }
        }
    }
}