.why{
    margin: 96px 0;
    &-inner{
        display: flex;
        justify-content: center;
        gap: 130px;
        @media (max-width: $breakpoint-lg) {
            flex-wrap: wrap;
            gap: 40px;
        }
        &__descr{
            &-title{
                font-size: 32px;
                font-weight: 600;
                color: #14293E;
                margin-bottom: 16px;
            }
            &-text{
                color: #496179;
                font-size: 18px;
                line-height: 24px;
                font-weight: 500;
                margin-bottom: 32px;
                max-width: 550px;
            }
            &-img{
                max-width: 550px;
                min-height: 368px;
                width: 100%;
            }
        }
        &__content{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 26px;
            &-item{
                display: flex;
                max-width: 664px;
                gap: 20px;
                align-items: center;
                padding: 16px;
                border-bottom: 1px solid #E8EBEE;
            }
            &-plus{
                width: 22px;
                height: 2px;
                background-color: #496179;
                position: relative;
                &::after{
                    content: "";
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    background-color: inherit;
                    width: 2px;
                    height: 22px;
                }
            }
            &-text{
                color: #496179;
                font-size: 20px;
                font-weight: 400;
                max-width: 584px;
            }
        }
    }
}