.about{
    margin-bottom: 95px;
    &__inner{
        &-content{
            display: flex;
            align-items: center;
            justify-content: space-between;
            @media (max-width: $breakpoint-lg){
                flex-direction: column;
                gap: 20px;

            }
        }
        &-info{
            max-width: 550px;
            margin-left: 48px;
            @media (max-width: $breakpoint-lg){
                margin: 0;
            }
        }
        &-subtitle{
            margin-bottom: 8px;
        }
        &-title{
            margin-bottom: 16px;
        }
        &-text{
            color: $text-clr;
            line-height: 32px;
            margin-bottom: 32px;
        }
        &-btn{
            border: none;
            background-color: $accent-clr;
            width: 325px;
            height: 55px;
            color: $second-white-clr;
            font-weight: 600;
            text-transform: capitalize;
            border-radius: 8px;

            @media (max-width: $breakpoint-sm){
                width: 300px;
            }
        }
        &-img{
            width: 100%;
        }
    }
    &__item{
        &-wrapper{
            margin-top: -4px;
            display: flex;
            @media (max-width: $breakpoint-lg){
                flex-wrap: wrap;
                align-items: center;
                justify-content: center;
            }
        }
        &-box{
            max-width: 480px;
            min-height: 544px;
            padding: 48px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            background-color: #F4F8FB;
            position: relative;
            overflow: hidden;

            &:nth-child(odd){
                background-color: #46B5B6;
            }
            &:hover{
                .about__item-img{
                    &--first{
                        transform: translate(100%, 120%) rotate(460deg);
                    }
                    &--second{
                        transform: translate(-70%, -150%) rotate(350deg);
                    }
                    &--third{
                        transform: translate(-70%, 120%) rotate(300deg);
                    }
                }

            }
        }
        &-title{
            font-size: 40px;
            line-height: 56px;
            font-weight: 500;
            color: #FBFDFE;
            max-width: 384px;
            position: relative;
            z-index: 2;
            &--second{
                color: $main-clr;
            }
        }
        &-info{
            display: flex;
            flex-direction: column;
            gap: 24px;
        }
        &-text{
            font-size: 18px;
            font-weight: 400;
            line-height: 28px;
            color: #F4F8FB;
            position: relative;
            z-index: 2;
            &--second{
                color: $text-clr;
            }
        }
        &-link{
            font-size: 18px;
            font-weight: 500;
            color: #F4F8FB;
            text-decoration: underline;
            cursor: pointer;
            position: relative;
            z-index: 2;
            max-width: 218px;
            padding: 0;
            margin: 0;
            &--second{
                color: $link-clr;
            }
        }
        &-img{
            position: absolute;
            z-index: 1;
            pointer-events: none;
            transition: transform 1.5s ease-in-out;
            &--first{
                top: -95px;
                left: -95px;
            }
            &--second{
                bottom: -90px;
                right: -80px;
            }
            &--third{
                top: -120px;
                right: -90px;
            }
        }

    }
}